
import Pay from '../../../base/pay/pay'
import MButton from '../../../base/button/button'
import Divider from 'vux/src/components/divider/index.vue'
import md5 from 'vux/src/tools/md5/index.js'

import { mapGetters, mapActions } from 'vuex'
import Vue from 'vue'
export default {
name: "login",
components: {
MButton,
Divider
},
mounted() {
this.phone = this.userPhone
},
watch: {
phone() {
this.checked()
},
pwd() {
this.checked()
}
},
data() {
return {
btnSaveState: true,
phone: this.userPhone,
pwd: ''
}
},
computed: {
...mapGetters([
'userPhone'
])
},
methods: {
checked() {
if ((/^1[34578]\d{9}$/.test(this.phone) && this.pwd.length >= 6)) {
this.btnSaveState = true
return false
}
this.btnSaveState = false
},
goRegister() {
this.$router.push({ name: 'registerOne' })
},
doLogin() {
if (this.btnSaveState) {
this.$vux.loading.show({
})
this.$axios.post('/api/user/login', {
telephone: this.phone,
password: md5(this.pwd)
}).then((response) => {
this.$vux.loading.hide()
let data = response.data
if (data.code !== "200") {
this.$vux.alert.show({
title: '错误提示',
content: data.message,
})
} else {

this.setUserInfo(data.data)

this.$router.push({ name: 'index' })
}
}).catch((error) => {
console.log(error)
})
}
},
forgetPwd() {
this.setUserPhoneHistory(this.phone)
this.$router.push({ name: 'forgetPwd' })
},
otherClick() {
this.$vux.alert.show({
title: '温馨提示',
content: '功能开发中',
buttonText: '我知道了'
})
},
...mapActions([
'setUserInfo',
'setUserState',
'setUserPhoneHistory'
])
}
}



